import React, { Component } from 'react'
import PropTypes from 'prop-types'
import NotificationService from 'Shared/services/NotificationService'

import { ASPECT_RATIOS, CROP_SIZES } from 'Shared/constants/Files'
import { getAcceptedAvatarFormats, MAX_UPLOAD_FILE_SIZE, ACCEPTED_AVATAR_FORMATS } from 'Shared/constants/S3'
import { dataURItoBlob, avatarFileName } from 'Shared/helpers/files'
import { parseFileName } from 'Shared/helpers/string'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'

import Uploader from './Uploader'
import EditorModal from './EditorModal'
import UploadedSingleFileWrapper from './UploadedSingleFileWrapper'
import Label from 'Shared/components/FormElements/Label'
import './SingleImageUploader.scss'

class AvatarUploader extends Component {
  static propTypes = {
    uploadFile: PropTypes.func.isRequired,
    deleteFile: PropTypes.func.isRequired,
    textConfig: PropTypes.shape({
      uploadButtonText: PropTypes.string,
      editorModalHeading: PropTypes.string,
      uploaderContent: PropTypes.string,
      deleteModalTitle: PropTypes.string,
      deleteModalContent: PropTypes.string
    }).isRequired,
    cropAspectRatio: PropTypes.number,
    layout: PropTypes.oneOf(['horizontal', 'vertical', 'profile']),
    sizeConstraints: PropTypes.arrayOf(PropTypes.number),
    requirement: PropTypes.bool,
    handleException: PropTypes.func
  }

  static defaultProps = {
    handleException: () => {},
    requirement: true,
    sizeConstraints: CROP_SIZES.AVATAR,
    cropAspectRatio: ASPECT_RATIOS.SQUARE,
    layout: 'horizontal',
    textConfig: {
      uploadButtonText: 'common.changeFile',
      uploaderContent: 'common.uploadFileInfo',
      editorModalTitle: 'common.cropImage',
      deleteModalTitle: 'remove.fileGeneric.title',
      deleteModalContent: 'remove.fileGeneric.description'
    }
  }

  constructor (props) {
    super(props)
    this.state = {
      file: null,
      showEditor: false,
      originalImage: null,
      changedImage: null
    }
  }

  _showEditorModal = () => this.setState({ showEditor: true })
  _hideEditorModal = () => this.setState({ showEditor: false })

  handleEditorModalClose = () => {
    this._hideEditorModal()
    this.resetCroppedImage()
  }

  onDrop = (acceptedFiles, rejectedFiles) => {
    if (!this.props.requirement) {
      return this.props.handleException()
    }

    const { multiple } = this.props

    if (!multiple && !acceptedFiles.length && rejectedFiles.some(file => file.size > MAX_UPLOAD_FILE_SIZE)) {
      return NotificationService.error(this.props.t('errors.imageTooLarge', { limit: MAX_UPLOAD_FILE_SIZE / Math.pow(1024, 2) }))
    }

    if (!multiple && !acceptedFiles.length && rejectedFiles.some(file => !ACCEPTED_AVATAR_FORMATS.includes(file.type))) {
      return NotificationService.error(this.props.t('errors.fileUnsupported'))
    }
  }

  onDropAccepted = acceptedFiles => {
    if (!this.props.requirement) {
      return this.props.handleException()
    }

    const reader = new window.FileReader()

    reader.onload = () => {
      this.setState({ originalImage: reader.result, file: acceptedFiles[0] })
    }
    reader.readAsDataURL(acceptedFiles[0])

    return this._showEditorModal()
  }

  resetCroppedImage = () => this.setState({ originalImage: null })

  uploadImage = async croppedImage => {
    await this.props.uploadFile(dataURItoBlob(croppedImage), parseFileName(this.state.file.name))
    this.setState({ changedImage: croppedImage })
  }

  render () {
    const { t, imageUrl, deleteFile, textConfig, multiple, hasError, error, label } = this.props
    const { originalImage, showEditor, changedImage } = this.state
    const uploaderClass = cx({
      'single-file-uploader__wrapper': true,
      'single-file-uploader__wrapper--error': hasError
    })

    const inputLabel = label || ''

    return (
      <div className={uploaderClass}>
        {(hasError || inputLabel) && <Label className='single-file-uploader__label'>{hasError ? t(error.key, error.options) : inputLabel }</Label>}
        {imageUrl
          ? <UploadedSingleFileWrapper
            layout={this.props.layout}
            sizeConstraints={this.props.sizeConstraints}
            uploadButtonText={t(textConfig.uploadButtonText)}
            deleteModalTitle={t(textConfig.deleteModalTitle)}
            deleteModalContent={t(textConfig.deleteModalContent)}
            img={changedImage || imageUrl}
            filename={avatarFileName(imageUrl)}
            onDropAccepted={this.onDropAccepted}
            onDrop={this.onDrop}
            onDelete={deleteFile}
          />
          : <Uploader
            i18nContentKey={textConfig.uploaderContent}
            uploadButtonText={t(textConfig.uploadButtonText)}
            maxFileSize={MAX_UPLOAD_FILE_SIZE}
            multiple={multiple}
            onDrop={this.onDrop}
            onDropAccepted={this.onDropAccepted}
            acceptedFormats={getAcceptedAvatarFormats()}
          />}
        <EditorModal
          title={t(textConfig.editorModalTitle)}
          sizeConstraints={this.props.sizeConstraints}
          cropAspectRatio={this.props.cropAspectRatio}
          hideModal={this.handleEditorModalClose}
          handleSubmit={this.uploadImage}
          isModalVisible={showEditor}
          file={originalImage}
        />
      </div>
    )
  }
}

export default withTranslations(AvatarUploader)



