import React, { Component } from 'react'
import Cropper from 'react-cropper'
import PropTypes from 'prop-types'
import { canvasToDataURL, resize } from 'Shared/helpers/image'

import Modal, { PreviewModalMobile } from 'Shared/components/Modal/index'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { ASPECT_RATIOS, CROP_SIZES } from 'Shared/constants/Files'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import './EditorModal.scss'
import Button, { PrimaryButton } from 'Shared/components/Button'

class EditorModal extends Component {
  static propTypes = {
    handleSubmit: PropTypes.func.isRequired,
    isModalVisible: PropTypes.bool.isRequired,
    hideModal: PropTypes.func.isRequired,
    cropAspectRatio: PropTypes.number.isRequired,
    sizeConstraints: PropTypes.arrayOf(PropTypes.number)
  }

  static defaultProps = {
    cropAspectRatio: ASPECT_RATIOS.SQUARE,
    sizeConstraints: CROP_SIZES.AVATAR
  }

  constructor (props) {
    super(props)
    this.state = {
      cropResult: false
    }
  }

  cropImage = () => {
    const [width, height] = this.props.sizeConstraints
    const croppedCanvas = this.cropper.getCroppedCanvas()
    const imageToUpload = canvasToDataURL(resize(croppedCanvas, { width, height }))

    this.props.handleSubmit(imageToUpload)
    this.props.hideModal()
  }

  getCropper = () => {
    const { file } = this.props
    return (
      <Cropper
        style={{ height: 400, width: '100%' }}
        aspectRatio={this.props.cropAspectRatio}
        guides={false}
        src={file}
        ref={cropper => { this.cropper = cropper }}
      />
    )
  }

  render () {
    const { t, hideModal, isModalVisible, title, isMobile } = this.props

    return (
      isMobile ? (
        <PreviewModalMobile
          title={title}
          handleClose={hideModal}
          show={isModalVisible}
        >
          {this.getCropper()}
          <div className='editor-modal-mobile__modal-buttons'>
            <Button onClick={hideModal}>{t('common.cancel')}</Button>
            <PrimaryButton onClick={this.cropImage}>{t('common.submit')}</PrimaryButton>
          </div>
        </PreviewModalMobile>
      ) : (
        <Modal
          title={title}
          handleSubmit={this.cropImage}
          handleClose={hideModal}
          show={isModalVisible}
          handleCancel={hideModal}
          cancelText={t('common.cancel')}
          submitText={t('common.save')}
          showContact={false}
          showCancel
        >
          {this.getCropper()}
        </Modal>
      )
    )
  }
}

export default withDeviceWidthDetector(withTranslations(EditorModal))



