import React, { Component } from 'react'
import cx from 'classnames'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label'
import Button, { BigJustIconButton } from 'Shared/components/Button'
import { getAcceptedAvatarFormats, MAX_UPLOAD_FILE_SIZE } from 'Shared/constants/S3'
import UploadButton from './UploadButton'
import DeleteModal from 'Shared/components/DeleteModal'
import { CROP_SIZES } from 'Shared/constants/Files'
import './UploadedSingleFileWrapper.scss'

class UploadedSingleFileWrapper extends Component {
  static propTypes = {
    layout: PropTypes.string,
    img: PropTypes.string.isRequired,
    filename: PropTypes.string.isRequired,
    onDelete: PropTypes.func.isRequired,
    onDropAccepted: PropTypes.func.isRequired,
    onDrop: PropTypes.func.isRequired,
    deleteModalTitle: PropTypes.string.isRequired,
    deleteModalContent: PropTypes.string.isRequired
  }

  static defaultProps = {
    sizeConstraints: CROP_SIZES.AVATAR
  }

  constructor (props) {
    super(props)
    this.state = {
      showDeleteModal: false
    }
  }

  _openDeleteModal = () => this.setState({ showDeleteModal: true })
  _hideDeleteModal = () => this.setState({ showDeleteModal: false })

  renderUploadedFileByLayout = () => {
    const {
      t,
      img,
      filename,
      onDropAccepted,
      onDrop,
      uploadButtonText,
      layout
    } = this.props

    const [width, height] = this.props.sizeConstraints

    const singleFileUploaderClass = cx({
      'single-file-uploader__file-container': true,
      'single-file-uploader__file-container--horizontal': layout === 'horizontal',
      'single-file-uploader__file-container--vertical': layout === 'vertical'
    })

    const verticalImageStyles = {
      backgroundImage: `url(${img})`,
      paddingBottom: `${height / width * 100}%`
    }

    switch (true) {
      case this.props.layout === 'horizontal':
        return (
          <div className={singleFileUploaderClass}>
            <img className='single-file-uploader__file' src={img} alt='Uploaded image - Oliv' />
            <div className='single-file-uploader__file-filename'>
              <Label>{t('common.uploadedFile')}</Label>
              <p className='single-file-uploader__file-name'>{filename}</p>
            </div>
            <div className='single-file-uploader__file-buttons'>
              <Button onClick={this._openDeleteModal} justIcon><i className='ion-md-trash' /></Button>
              <UploadButton
                uploadButtonText={uploadButtonText}
                onDrop={onDrop}
                onDropAccepted={onDropAccepted}
                acceptedFormats={getAcceptedAvatarFormats()}
                maxFileSize={MAX_UPLOAD_FILE_SIZE}
              />
            </div>
          </div>
        )
      case this.props.layout === 'profile':
        return (
          <div className='single-file-uploader__file-container--profile'>
            <img
              className='single-file-uploader__file--profile'
              src={img}
              alt='Uploaded image - Oliv'
              style={{
                width: '110px',
                height: '110px',
                borderRadius: '50%'
              }}
            />
            <div className='single-file-uploader__file-buttons--profile'>
              <UploadButton
                customButton={BigJustIconButton}
                customButtonChildren={<i className='icon-im-edit' />}
                onDrop={onDrop}
                onDropAccepted={onDropAccepted}
                acceptedFormats={getAcceptedAvatarFormats()}
                maxFileSize={MAX_UPLOAD_FILE_SIZE}
              />
              <BigJustIconButton onClick={this._openDeleteModal}><i className='ion-md-trash' /></BigJustIconButton>
            </div>
          </div>
        )
      default:
        return (
          <div className={singleFileUploaderClass}>
            <div className='single-file-uploader__file' alt='Uploaded image - Oliv' style={verticalImageStyles} />
            <div className='single-file-uploader__file-filename'>
              <Label>{t('common.uploadedFile')}</Label>
              <p className='single-file-uploader__file-name'>{filename}</p>
            </div>
            <div className='single-file-uploader__file-buttons'>
              <Button onClick={this._openDeleteModal} justIcon><i className='ion-md-trash' /></Button>
              <UploadButton
                uploadButtonText={uploadButtonText}
                onDrop={onDrop}
                onDropAccepted={onDropAccepted}
                acceptedFormats={getAcceptedAvatarFormats()}
                maxFileSize={MAX_UPLOAD_FILE_SIZE}
              />
            </div>
          </div>
        )
    }
  }

  render () {
    const {
      onDelete,
      deleteModalTitle,
      deleteModalContent
    } = this.props

    return (
      <div>
        {this.renderUploadedFileByLayout()}
        <DeleteModal
          title={deleteModalTitle}
          content={deleteModalContent}
          handleDelete={onDelete}
          isModalVisible={this.state.showDeleteModal}
          hideDeleteModal={this._hideDeleteModal}
        />
      </div>
    )
  }
}

export default withTranslations(UploadedSingleFileWrapper)



