import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Dropzone from 'react-dropzone'
import { Interpolate } from 'react-i18next'
import withTranslations from 'Shared/hoc/withTranslations/index'
import './Uploader.scss'

class Uploader extends Component {
  static propTypes = {
    children: PropTypes.node,
    onDrop: PropTypes.func,
    onDropAccepted: PropTypes.func,
    acceptedFormats: PropTypes.string,
    maxFileSize: PropTypes.number,
    i18nContentKey: PropTypes.string,
    uploadButtonText: PropTypes.string
  }

  static defaultProps = {
    multiple: true
  }

  uploadButton = <span className='file-input__button--upload' onClick={() => this.dropzoneRef.open()}>{this.props.t('common.choose')}</span>

  render () {
    const { children, onDrop, onDropAccepted, acceptedFormats, maxFileSize, i18nContentKey, multiple } = this.props

    return (
      <div className='single-file-uploader'>
        <Dropzone
          {...this.props.input}
          maxSize={maxFileSize}
          multiple={multiple}
          accept={acceptedFormats}
          onDropAccepted={onDropAccepted}
          className='file-input__dropzone'
          activeClassName='file-input__dropzone--active'
          disableClick
          ref={(node) => {
            this.dropzoneRef = node
          }}
          onDrop={onDrop}
        >
          <div className='file-input__container'>
            <div className='file-input__drop-area'>
              <i className='icon-im-upload' />
              <Interpolate
                parent='div'
                i18nKey={i18nContentKey}
                className='file-input__description'
                uploadButton={this.uploadButton}
              />
            </div>
          </div>
        </Dropzone>
        {children}
      </div>
    )
  }
}

export default withTranslations(Uploader)



