import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Dropzone from 'react-dropzone'
import { PrimaryButtonOutline } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations/index'
import './UploadButton.scss'

class Uploader extends Component {
  static propTypes = {
    children: PropTypes.node,
    onDrop: PropTypes.func,
    onDropAccepted: PropTypes.func,
    acceptedFormats: PropTypes.string,
    maxFileSize: PropTypes.number
  }

  render () {
    const { t, onDrop, onDropAccepted, acceptedFormats, maxFileSize, uploadButtonText, customButton, customButtonChildren } = this.props

    const CustomButton = customButton

    return (
      <div className='single-file-uploader__button'>
        <Dropzone
          maxSize={maxFileSize}
          accept={acceptedFormats}
          onDropAccepted={onDropAccepted}
          className='single-file-uploader__button'
          activeClassName='single-file-uploader__button--active'
          disableClick
          ref={(node) => {
            this.dropzoneRef = node
          }}
          onDrop={onDrop}
        >
          {
            customButton ? (
              <CustomButton onClick={() => this.dropzoneRef.open()}>{customButtonChildren}</CustomButton>
            ) : (
              <PrimaryButtonOutline onClick={() => this.dropzoneRef.open()}>{t(uploadButtonText)}</PrimaryButtonOutline>
            )
          }
        </Dropzone>
      </div>
    )
  }
}

export default withTranslations(Uploader)



